<template>
  <div class="index">
    <h1 style="margin: 20px 0;">Index Component</h1>

    <div class="paging">
      <Pagination v-bind:pageinfo="pageinfo" v-on:switchpage="switchPage"/>
    </div>
    

    <div class="sidebarlist">
      <div class="sidebar">
        <ul>
          <li class="title"></li>
          <li v-bind:class="{list:true,lighted:tag==0}" value="0" v-on:click="getArticle"></li>
          <li v-bind:class="{list:true,lighted:tag==1}" value="1" v-on:click="getArticle"></li>
          <li v-bind:class="{list:true,lighted:tag==2}" value="2" v-on:click="getArticle"></li>
        </ul>
        <ul>
          <li class="title"></li>
          <li v-for="(tagitem,index) in articletags" v-bind:key="index" v-bind:value="index+3" v-bind:class="{list:true,lighted:tag==index+3}" v-on:click="getArticle">
          {{tagitem.tag_name}}
          </li>
        </ul>
      </div>
    </div>

    <div class="content">
      <h2>indexContent</h2>
      <div class="articleItem" v-for="(article,index) in articles" v-bind:key="index">
        <div class="articleTitle" v-on:click="goColumnItem(index)">
          <!---->
          {{article.article_title}}
        </div>
        <div class="articleAuthor">
          author:{{article.article_author}}
        </div>
        <div class="articleContent">
          {{article.article_content}}
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import articletags from '../../mock/article/articletags.js'
import Pagination from '@/components/main/Pagination.vue'
export default {
  name: 'Index',
  components:{
    Pagination
  },
  data:function(){
    return {
      articletags:articletags,
      tag: 0,

      //pagination
      pageinfo:{
        pagenum:7,
        left:0,
        currentpage:0,
      }
      //pageList:[1,2,3,4,5,6,7],
    }
  },
  computed:{
    articles:function(){
      return this.$store.getters.getArticlesByTag(this.tag)
    }
  },
  methods:{
    getArticle:function(event){
      //listvalue
      let tag = event.target.value
      this.tag=tag
      //console.log(this.tag)
    },
    switchPage(switchinfo){
      if(switchinfo=="addPage"){
        let pagenum=this.pageinfo.pagenum
        if(this.pageinfo.currentpage<pagenum-1){
          if(this.pageinfo.currentpage<pagenum-3&&this.pageinfo.currentpage>1){
            this.pageinfo.left=this.pageinfo.left-80
          }
          this.pageinfo.currentpage++
        }
      }else if(switchinfo=="subPage"){
        let pagenum=this.pageinfo.pagenum
        if(this.pageinfo.currentpage>0){
          if(this.pageinfo.currentpage<pagenum-2&&this.pageinfo.currentpage>2){
            this.pageinfo.left=this.pageinfo.left+80
          }
          this.pageinfo.currentpage--
        } 
      }
    },
  }
}
</script>

